:root {
	/* color pallete */
	--light-grey: #eee;
	--light-grey-rgb: 238, 238, 238;
	--yellow: #ffc70b;
	--yellow-rgb: 255, 199, 11;
	--turqoise: #0bebff;
	--turqoise-rgb: 60, 213, 225;
	--dark-turqoise: #009eaa;
	--dark-turqoise-rgb: 0, 158, 170;

	/* spacing */
	--space-eighth: 0.2rem;
	--space-quarter: 0.4rem;
	--space-half: 0.8rem;
	--space-default: 1.6rem;
	--space-double: 3.2rem;
	--space-quad: 6.4rem;
	--space-oct: 12.8rem;

	/* font-weights */
	--weight-light: 300;
	--weight-regular: 400;
	--weight-bold: 700;
	--weight-bolder: 900;
}

/* reset */
html {
	box-sizing: border-box;
	font-size: 62.5%;
}

*, *::before, *::after {
	box-sizing: inherit;
}

html, body {
	height: 100%;
}

body {
	font-family: 'Ubuntu', sans-serif;
	font-size: 1.6rem;
	background-color: #fff;
	margin: 0;
}

p {
	margin: 0 !important;
}

.ui.error>input {
	border: 1px solid red !important;
}
.ui.label.label {
	width: 100px;
	text-align: center;
}
.field {
	margin-bottom: 1.6rem !important;
	margin-bottom: var(--space-default) !important;
}
.field > .ui.labeled.input {
	width: calc(100% - 1.6rem) !important;
	width: calc(100% - var(--space-default)) !important;
}
.ui.labeled.input:not([class*="corner labeled"]) .label:first-child {
	background-color: #fff !important;
	color: #009eaa !important;
	color: var(--dark-turqoise) !important;
	border: 1px solid #009eaa !important;
	border: 1px solid var(--dark-turqoise) !important;
	border-right: 0 !important;;
}
.ui.labeled.input:not([class*="corner labeled"]) .label:first-child+input {	
	border: 1px solid #009eaa !important;	
	border: 1px solid var(--dark-turqoise) !important;
